import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Angular2TokenService } from 'angular2-token';
import { routes } from '../routes';

/*
  Generated class for the PymesModuleProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/
@Injectable()
export class PymesModuleProvider {

  constructor(public http: HttpClient, public _tokenService: Angular2TokenService) {
 
  }

  getPymes(){
  return  this._tokenService.get(routes.pymes());
  }
}



